@media only screen and (min-width: 900px) {



    //VARS

    $accent-color: #13404E; 
    $side-margin: 5vw;


    //UNI

    .o-block{
        border-radius: 15px;
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 100%;
        color: #fff;
        margin: 0;

        & > a{
            display: block;
        }

        &.--intro{
            height: 100vh !important;

            & > a > img, & > img{
                width: 100%;
                transform: translate(0, -25%) !important;
            }
        }

        
        &.--hero{
            height: 100vh !important;
            & > a > img, & > img{
                width: 100% !important;
                transform: translate(0, -5%) !important;
            }

            h2{
                font-size: 4em !important;
            }
        }

        &.--full-width{
            height: auto;
    
            & > a > img, & > img{
                width: 100%;
                transform: translate(0, 0);
            }

            .c-block-content{
                &.--center{
                    h2{
                        font-size: 4em !important;
                    }
    
                    .o-block-author{
                        left: 4vw;
                        bottom: 7vh;
                    }

                    .o-block-text{
                        transform: translate(-50%, -50%);
                    }
                }
            }
    
            .o-block-text{
                position: absolute;
                top: 50%;
                left: 3vw;
                transform: translate(0, -50%);
                z-index: 2;
            }

            .o-block-author{
                position: absolute;
                bottom: 8%;
                left: 3vw;
                z-index: 2;
            }
        }
    
        & > img, & > a > img{
            width: 100%;
        }
    
        .c-block-content{
            &.--small{
                padding: 4vh 1.2vw;
    
                .o-block-text{
        
                    h2{
                        font-size: 20px;
                        margin-bottom: 30px;
                    }
    
                    p{
                        font-size: 14px;
                    }
                }
            }

            &.--medium{
                padding: 6vh 2vw 2vw 2vw;
    
                .o-block-text{
        
                    h2{
                        font-size: 28px;
                        //width: 80%;
                        margin-bottom: 30px;
                    }
    
                    p{
                        font-size: 14px;
                        //width: 70%;
                    }
                }
            }
        
            &.--large{
                padding: 10vh 3vw;
    
                .o-block-text{
        
                    h2{
                        font-size: 45px;
                        //width: 80%;
                        margin-bottom: 40px;
                    }
        
                    p{
                        font-size: 20px;
                        //width: 70%;
                    }
                }
            }

            &.--center{
                .o-block-text{
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    z-index: 2;
                    width: 80%;
    
                    h2{
                        text-align: center;
                        font-size: 2em;
                    }

                    p{
                        text-align: left;
                        display: block;
                    }
                }
            }
    
        }
    
        .o-block-text{
            color: #fff;
            width: unset;
            
            h2, p{
                text-align: left;
            }
        }
    
        .o-block-author{
            display: flex;
            align-items: center;
            z-index: 2;
    
            p{
                margin-right: 12px;
            }
    
            img{
                width: 50px;
            }
        }
    
        .o-btn{
            position: absolute;
            left: 50%;
            transform: translate(-50%, 0);
            bottom: 12%;
            z-index: 2;
        }
    
        .o-cover{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #fff;
            pointer-events: none;
            transform: translate(0, 0);
            z-index: 4;
        }
    }

    .o-flex{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
    
        &.--two{
            & > div{
                flex: 48%;

                &:first-child{
                    padding-right: 18px;
                }

                &:last-child{
                    padding-left: 18px;
                }
            }
        }

        &.--three{
            & > div{
                flex: 28%;
            }
        }

        &.--four{
            & > div{
                flex: 20%;
            }
        }
    }

    .o-row{
        padding-bottom: 30px;

        &.--three{
            & > div:nth-child(2){
                margin: 0 28px;
            }
        }

        &.--four{
            & > div:nth-child(2){
                margin: 0 22px;
            }

            & > div:nth-child(3){
                margin: 0 22px 0 0;
            }
        }
    }

    .c-full-title{
        margin-top: 12vh;
        padding: 0 7vw;

        h2{
            font-size: 35px;
        }
    
        a{
            margin-top: 30px;
            width: 30%;
        }
    }

    .o-sec-title{
        &.--center{
            text-align: left;
        }
    }

    .c-btn{
        &.--center{
            width: 30%; 
            margin: 6vh auto -5vh auto;
        }
    }

    .--width-50{
        width: 60% !important;
        height: auto;
    }



    //NAV
    

    .c-nav{
        padding-top: 40px;
        position: relative;

        .c-type-1{
            // display: block;
            display: none;
            

            .c-mobile{
                margin-bottom: 30px;

                a{
                    display: block;

                    img{
                        width: 196px;
                    }
                }

                button{
                    display: none;
                }
            }

            .c-dropdown{
                display: flex;
                width: 100%;
                align-items: flex-start;
                justify-content: space-between;

                & > ul{
                    display: flex;
                    align-items: flex-start;

                    li{
                        margin: 0 30px 0 0;
                        position: relative;

                        &:hover div{
                            opacity: 1;
                            visibility: visible;
                            transform: translate(-40px, 18px);
                        }


                        div{
                            opacity: 0;
                            transition: .4s;
                            transform: translate(-40px, 30px);
                            background-color: #fff;
                            padding: 20px;
                            visibility: hidden;
                            position: absolute;
                            border-radius: 8px;
                            border: 2px solid $accent-color;
                            
                            li a{
                                font-size: 14px; 
                                width: 150px;
                                margin: 16px 0; 
                                display: block;
                            }
                        } 
                    }
                    
                }
            }
        }

        .c-type-2{
            div:nth-child(2){
                img{
                    width: 195px;
                }
            }
        }

    }


    .c-fit-menu{
        width: 90%;
        height: auto;
        overflow-y: auto;
        position: absolute;
        top: 15%;
        left: 5%;
        z-index: 11;
        background: #fff;
        padding: 10vh $side-margin;
        border-radius: 8px;
        border: 3px solid $accent-color;
    
        & > div{
            display: flex;
            justify-content: space-between;

            & > div:last-child{
                transform: translate(-55%, 0);
            }
        }

        & > div:last-child{
            img{
                width: 180px;
            }
        }
    }


    // MAIN

    main{
        padding: 2vw $side-margin;
    }

    //HERO

    .c-hero{
        display: grid; 
        grid-template-columns: 1fr 1.2fr .8fr; 
        grid-template-rows: 1fr 1fr 1fr; 
        gap: 18px 18px; 
        grid-template-areas: 
          "main main side-1"
          "main main side-2"
          "Bottom Bottom side-2"; 

        height: 155vh;

        .second-box{
            display: grid; 
            grid-template-columns: 1fr 1fr 1fr; 
            grid-template-rows: 1fr 1fr 1fr; 
            gap: 0 18px; 
            height: 50vh;
            grid-template-areas: 
                "one two three"
                "one two three"
                "one two three"; 
            grid-area: Bottom; 
        }
    
        .main-box{
            grid-area: main;
            width: 100%;
            
            .o-block{
                height: 100%;
            }
        }
        .side-box-1{
            grid-area: side-1;
            // height: 52vh;
        }
        .side-box-2{
            grid-area: side-2;

            .o-btns{
                position: absolute;
                bottom: 5%;
                width: 100%;
                left: 0;
                padding: 0 20px;
                text-align: center;
                z-index: 2;
            
                a{
                    display: block;
                    width: 100%;
                    padding: 25px 0;
                    text-align: center;
                    background-color: #267F69;
                    border-radius: 8px;
                    margin-bottom: 20px;
        
                    &:last-child{
                        background: none;
        
                    }
                }
            }
        }
    
        .one{grid-area: one;}
        .two{grid-area: two;}
        .three{grid-area: three;}
    }
    
    

    //C-QUIZ

    .c-quiz{
        margin-bottom: 15vh;
        .o-block{
            margin: 0;
        }

        .--small{
            .o-block-text{
                width: 60%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: left;

                h2{
                    margin-bottom: 0 !important;
                }
            }
        }
    }


    .container{
        display: grid; 
        grid-template-columns: 1fr 1fr 1fr 1fr; 
        grid-template-rows: 0.2fr 1.2fr 1.2fr 1.2fr 1.2fr; 
        gap: 20px 20px; 
        height: 100vh;
    }

    .title-search{ 
        grid-area: 1 / 2 / 2 / 5; 

        div{
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }
    .title { grid-area: 1 / 1 / 2 / 2; }
    .quiz1 { grid-area: 2 / 1 / 3 / 2; }
    .quiz2 { grid-area: 3 / 1 / 4 / 2; }
    .quiz3 { grid-area: 4 / 1 / 5 / 2; }
    .quiz4 { grid-area: 5 / 1 / 6 / 2; }
    .atoz { grid-area: 2 / 2 / 4 / 5; }
    .dis1 { grid-area: 4 / 2 / 6 / 3; }
    .dis2 { grid-area: 4 / 3 / 6 / 4; }
    .dis3 { grid-area: 4 / 4 / 6 / 5; }
      
      
    .c-atoz{
        display: block;
    
        ul{
            display: flex;
            li{
                aspect-ratio: 1;
                background-color: #267F69;
                border-radius: 100px;
    
                a{
                    height: 100%;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    color: #fff;
                }
            }
        }
    
        &.--small{
            li{
                width: 8%;
                margin: 0 1% 3% 1%;
            }
        }
    }



    //FEATURES

    .c-features{
        padding: 5vh 5vw;

        & > div:first-child{
            padding-right: 15%;
            h3{
                font-size: 30px;
                margin-bottom: 80px;
                text-align: left;
            }

            .c-btn{
                width: 60%;
                margin: 0;
            }
        }

        & > div:last-child{
            & > div{
                display: flex;    

                div{
                    margin: 30px 35px;
                }

                img{
                    width: 80px;
                }
            }
            
        }
    }



    //TEAM

    .c-team{

        .o-flex{
            justify-content: space-between;
            margin: 8vh 0;

            & > div{
                flex: none;

                img{
                    width: 120px;
                }

                div{
                    margin-left: 20px;

                    p{
                        font-size: 18px;

                        &:last-child{
                            opacity: .3;
                        }
                    }
                }
            }
        }
    }



    //SIGNUP

    .c-signup{
        margin: 20vh 0 0 0;

        .o-flex{
            & > div:first-child{
                width: 40%;
                padding-right: 15%;
                transform: translate(0, -20%);
                h2{
                    font-size: 55px;
                    text-align: left;
                }

                form{
                    width: 74%;
                    margin-top: 50px;
                }

                input{
                    border: none;
                    width: 100%;
                    border-bottom: 2px solid #ddd;
                    font-size: 20px;
                    padding: 10px 0;
                    margin-bottom: 30px;
                }

                button{
                    width: 100%;
                }

            }

            & > div:last-child{
                .o-block{
                    width: 70%;

                    &:last-child{
                        transform: translate(40%, -30%);
                    }
                }
            }
        }
    }

    // FOOTER
    
    footer{
        height: 80vh;
        margin: 5vw;
        margin-top: -4vh;
        justify-content: space-between;
        padding: 5vw;

        ul{
            padding: 0;
        }

        .o-flex{
            align-items: flex-start;
        }

        & > div{
            height: 100%;
        }

        & > div:first-child{
            // width: 20%;
            flex: 30% !important;
            margin: 0;

            img{
                width: 200px;
            }
        }

        & > div:last-child{
            flex: 68% !important;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            & > div:last-child{
                text-align: left;
            }
        }
    }



    // VIDEO

    .c-video{
        display: block;

        .o-block-text{
            h2{
                font-size: 50px !important;
            }
    
            p{
                font-size: 25px !important;
                margin-bottom: 40px;
            }
        }
    }


    // PROMISE

    .c-promise{
        .o-flex{
            margin-bottom: 15%;
            & > div:nth-child(2), & > div:nth-child(4){
                transform: translate(0, 30%);
            }
        }
    }



    // POST

    .c-post{
        padding: 0 5vw;

        h3{
            color: #2FBE7E;
            font-size: 35px;
            text-align: center;
            margin: 40px 0;
        }
        
        p{
            font-size: 18px;
            color:rgba(140, 140, 140);
            padding: 20px 0;
        }
    }



    //TAGS

    .o-tags{
        display: flex;
        align-items: baseline;
        margin-bottom: 4vh;

        div{
            display: block;
        }

        div ul{
            display: flex;
            align-items: center;
            margin-left: 30px;

            li{
                a{
                    padding: 25px 45px;
                    border-radius: 100px;
                    margin: 20px;
                    background-color: #2FBE7E;
                    color: #fff;
                }
            }
        }
    }




    // TEAM PAGE

    .c-team-mem{
        display: flex;
        margin: 35px 0;

        img{
            width: 26%;
            margin-left: 0;
        }

        div{
            padding-left: 4vw;
            padding-top: 4vw;
            width: 60%;
            text-align: left;
        }

        h2{
            margin-top: 0px;
            span{
                display: inline-block;
                font-size: 15px;
                color: #888;
                margin-left: 20px;
            }
        }

        p{
            color: #808080;
            margin: 15px 0 20px 0;
        }
    }



    // FIT

    .o-fit-search{
        display: flex;
        align-items: center;
        justify-content: space-between;

        h2{
            margin-bottom: 0;
        }
    }

    

}